<template>
<div class="user-profile">
   <van-nav-bar title="个人信息" left-arrow @click-left="$router.back()" fixed />
   <div class="main-wrap">
    <van-cell-group>
  <van-cell title="头像" is-link @click="$refs['file'].click()">
    <template #default>
      <van-image class="avatar" round fit="cover"
        :src="user.photo"/>
    </template>
  </van-cell>
  <van-cell title="昵称" is-link :value="user.name" @click="isUpdataNameShow=true" />
  <van-cell title="性别" is-link :value="user.gender === 0 ? '男': '女'" @click="isUpdataGenderShow=true" />
  <van-cell title="生日" is-link :value="user.birthday" @click="isUpdataBirthdayShow=true"  />
  <!-- 一般情况下，因为要考虑服务器所在的时区/前端客户端所在的时区，所展示的时间保持统一。后端返回
  过来的时间参数，一定要叫她们转换为调整好时区的字符串 -->
</van-cell-group>
   </div>

   <!-- 编辑昵称弹出层 -->
   <van-popup v-model="isUpdataNameShow"
   position="bottom" :style="{ height: '100%' }">
   <UpdataName @close="isUpdataNameShow=false" v-model="user.name"></UpdataName>
   </van-popup>

      <!-- 编辑性别弹出层 -->
   <van-popup v-model="isUpdataGenderShow"
   position="bottom" :style="{ height: '40%' }">
     <UpdataGender @close="isUpdataGenderShow=false" v-model="user.gender"></UpdataGender>
   </van-popup>

   <!-- 编辑生日弹出层 -->
   <van-popup v-model="isUpdataBirthdayShow"
   position="bottom"  :style="{ height: '100%' }">
     <UpdataBirthday @close="isUpdataBirthdayShow=false" v-model="user.birthday"
    ></UpdataBirthday>
   </van-popup>

   <!-- 头像上传 -->
   <!-- 上传控件 需要用ref和$refs获取-->
   <input type="file" hidden ref="file" @change="onFileChange" />
   <!-- 编辑头像弹出层 -->
   <van-popup v-model="isUpdatePhotoShow" :style="{ height: '100%' }"
   position="bottom">
     <UpdatePhoto v-if="isUpdatePhotoShow" @close="isUpdatePhotoShow=false" v-model="image"
      @update-photo="user.photo=$event"></UpdatePhoto>
   </van-popup>
</div>
</template>

<script>
import UpdataName from './components/updat-name.vue'
import UpdataGender from './components/update-gender.vue'
import UpdataBirthday from './components/update-birthday.vue'
import UpdatePhoto from './components/update-photo.vue'
import { getUserProfileAPI } from '../../api/index.js'
export default {
  name: 'UserProfile',
  components: {
    UpdataName,
    UpdataGender,
    UpdataBirthday,
    UpdatePhoto
  },
  data () {
    return {
      user: {}, // 个人信息
      // user.gender 0表示男， 1表示女
      isUpdataNameShow: false, // 编辑昵称
      isUpdataGenderShow: false, // 编辑性别
      isUpdataBirthdayShow: false, // 编辑生日
      isUpdatePhotoShow: false, // 编辑图像
      image: null // 临时存放图片
    }
  },
  methods: {
    async loadProfile () {
      try {
        const { data: res } = await getUserProfileAPI()
        this.user = res.data
      } catch (error) {
        this.$toast('编辑资料失败')
      }
    },
    // 上传图片
    onFileChange () {
      // 1.获取文件对象
      const file = this.$refs.file.files[0]
      // 2.基于文件对象获取 blob数据
      this.image = window.URL.createObjectURL(file)
      // 3.当使用完成后，清空上传控件的value值，防止后续操作受到影响
      this.$refs.file.value = ''
      // 4.展示预览图像编辑弹出层
      this.isUpdatePhotoShow = true
    }
  },
  created () {
    // 获取用户基本信息
    this.loadProfile()
  }

}
</script>

<style lang="less" scoped>
.user-profile{
  .main-wrap{
    padding-top:94px;
  }
  .avatar{
    width: 52px;
    height: 52px;
  }
  .van-popup{
    background-color: #f2f2f2;
  }
}
</style>
