<template>
  <div class="update-photo">
   <img :src="value" class="img" ref="img">
    <div class='toolbar'>
       <span class="cancel" @click="$emit('close')">取消</span>
       <span class="confirm" @click="onConfirm">完成</span>
    </div>
  </div>
</template>

<script>
import { updateUserphotoAPI } from '../../../api/index.js'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
export default {
  name: 'UpdatePhoto',
  props: {
    // 如果父组件传过来的数据类型为Object但是这个Object不是基本对象，那么默认值default为null
    value: {
      type: String, // 头像的blob数据是字符串
      default: ''
    }
  },
  data () {
    return {
      cropper: null
    }
  },
  mounted () {
    // 初始化剪裁工具
    this.initCropper()
  },
  methods: {
    initCropper () {
      // 1. 获取页面图片dom元素
      const IMAGE = this.$refs.img
      // 2. 初始化裁剪
      this.cropper = new Cropper(IMAGE, {
        // 裁剪工具配置项 config
        viewMode: 1, // 定义裁剪模式。0表示裁剪框可以扩展到画布外，1,2,3表示只能在画布内
        dragMode: 'move', // 拖动模式 move表示移动画布（图片）
        aspectRatio: 1, // 裁剪比例 1是1:1的简写
        cropBoxMovable: false, // 裁图区域客服移动
        cropBoxResizable: false, // 裁图区域可否缩放
        background: false, // 是否显示默认背景色
        movable: true // 背景图可否移动 ，默认为true
        /* crop (event) { // 当移动图片时会触发的方法
          const eventObj = {
            x: event.detail.x,
            y: event.detail.y,
            width: event.detail.width,
            height: event.detail.height,
            rotate: event.detail.rotate,
            scaleX: event.detail.scaleX,
            scaleY: event.detail.scaleY
          }
          console.log(eventObj)
        } */
      })
    },
    onConfirm () {
      // 方案1 ：后端处理
      // 1.获取坐标数据
      // this.cropper.getData()
      // 2.将获取的坐标数发送到接口

      // 方案2：前端处理
      // 1.通过 this.cropper.getCroppedCanvas().toBlob() 方法流程，让裁剪区域中的图像进行处理
      this.cropper.getCroppedCanvas().toBlob((blob) => {
        // 2.获取到图像blob对象后，进行接口的调用和图片的上传
        this.updateUserphoto(blob)
      })
    },
    async updateUserphoto (blob) {
      // 加载提示
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 禁止背景点击
        duration: 0 // 持续展示 toast
      })
      // 3.如果接口请求是发送文件，那么需要以FormData对象进行数据传输
      const formData = new FormData()
      // 4. 将blob存放到formData中，formData.append('key属性名','value属性值')
      formData.append('photo', blob)
      try {
        const { data: res } = await updateUserphotoAPI(formData)
        // 触发父组件的自定义事件，来修改个人信息对象中的photo属性，实现图片的切换
        this.$emit('update-photo', res.data.photo)
        // 提示成功
        this.$toast.success('更新头像成功')
        // 改变弹出层
        this.$emit('close')
      } catch (error) {
        // 提示失败
        this.$toast.fail('更新头像失败')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.update-photo{
  background-color: #001;
  height: 100%;
  .toolbar{
   position: fixed;
   right:0;
   left:0;
   bottom:0;
   display: flex;
   justify-content: space-between;
  color:#fff;
   .cancel,.confirm{
       font-size: 40px;
       margin: 12px;
      }
   }
   .img{
   display: block; // 前端裁剪图片需要这两个样式
   max-width: 100%; // 前端裁剪图片需要这两个样式
   }
}
</style>
