<template>
<div>
<van-picker
  title="标题"
  show-toolbar
  :columns="columns"
  @confirm="onConfirm"
  @cancel="$emit('close')"
  :default-index="localGender"
/>
</div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
export default {
  name: 'UpdateGender',
  props: {
    value: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      localGender: this.value,
      columns: ['男', '女']
    }
  },
  methods: {
    async onConfirm (item, index) {
      /* value是文本 index是后端接口接收的数据 */
      // 加载提示
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 禁止背景点击
        duration: 0 // 持续展示 toast
      })
      try {
        await updateUserProfileAPI({
          gender: index
        })
        // 更新昵称视图内容
        this.$emit('input', index)
        // 关闭弹出层
        this.$emit('close')
        this.$toast.success('保存成功')
      } catch (error) {
        this.$toast.fail('修改性别失败')
      }
    }
  }
}
</script>

