<template>
<div class="updata-name">
<van-nav-bar
title="编辑昵称"
left-text="取消"
right-text="确认"
@click-left="$emit('close')"
@click-right="onConfirm"
/>
<!-- 输入框内容 -->
 <div class="updata-name-text">
  <van-field
  v-model.trim="localName"
  rows="2"
  autosize
  type="textarea"
  maxlength="10"
  placeholder="请输入昵称"
  show-word-limit
/>
 </div>
</div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
export default {
  name: 'UpdataName',
  props: { // 通过v-model获取用户数据中的昵称，后期可以通过$emit(input)进行修改
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      localName: this.value // 进去后，让输入框的内容为旧的昵称
    }
  },
  methods: {
    // 确认修改
    async onConfirm () {
      // 加载提示
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 禁止背景点击
        duration: 0 // 持续展示 toast
      })
      try {
        if (!this.localName.length) {
          this.$toast('昵称不能为空')
          return
        }
        await updateUserProfileAPI({
          name: this.localName
        })
        // 更新昵称视图内容
        this.$emit('input', this.localName)
        // 关闭弹出层
        this.$emit('close')
        this.$toast.success('保存成功')
      } catch (error) {
        // 如果报错，且返回值为409表示昵称已存在
        if (error.response && error.response.status === 409) {
          this.$toast.fail('该昵称已存在')
        } else {
          this.$toast.fail('修改昵称失败')
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.updata-name{
  .van-nav-bar {
    background-color: #fff;
      :deep(.van-nav-bar__title) {
        color: #000;
       }
      .van-icon{
        color:#3296fa;
       }
    }
      .updata-name-text{
        padding: 20px;
      }
}
</style>
