<template>
<div class="updata-birthday">
  <van-datetime-picker
  type="date"
  title="选择年月日"
  :min-date="minDate"
  :max-date="maxDate"
  @confirm="onConfirm"
  @cancel="$emit('close')"
  v-model="localBirthday"
/>
</div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
import dayjs from 'dayjs'
export default {
  name: 'UpdateBirthday',
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      localBirthday: this.value, // 选中的时间
      minDate: new Date(1917, 0, 1), // 开始年月日
      maxDate: new Date() // 结束年月日
    }
  },
  methods: {
    // 确认修改
    async onConfirm (value) {
      // 加载提示
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 禁止背景点击
        duration: 0 // 持续展示 toast
      })
      const newlocalBirthday = dayjs(value).format('YYYY-MM-DD') // 需要对数据进行一次处理
      try {
        await updateUserProfileAPI({
          birthday: newlocalBirthday
        })
        // 更新生日视图内容
        this.$emit('input', newlocalBirthday)
        // 关闭弹出层
        this.$emit('close')
        this.$toast.success('保存成功')
      } catch (error) {
        this.$toast.fail('修改生日失败')
      }
    }
  }
}
</script>

